'use stirct';

//==============================================================================
//                              Utils
//==============================================================================
// dom{{{
const dom = (selector) => document.querySelector(selector);
const doms = (selector) => document.querySelectorAll(selector);
/*}}}*/
// observer dom{{{
const observer_dom = (dom, action_is_intersecting, action_not_intersecting) => {
   const io = new IntersectionObserver((entries, io) => {
      entries.forEach((entry) => {
         if (entry.isIntersecting) {
            action_is_intersecting(entry.target);
            if (!action_not_intersecting) io.disconnect();
         } else if (action_not_intersecting) {
            action_not_intersecting(entry.target);
         }
      });
   });
   io.observe(dom);
};
/*}}}*/
// toggle navigation bar{{{
const toggle_navigation_bar = () => {
   window.addEventListener('scroll', () => {
      if (window.scrollY > window.position_old) {
         if (!window.nav_hidden) {
            dom('header').style.transform = 'translateY(-100%)';
            window.nav_hidden = true;
         }
      } else if (window.nav_hidden) {
         dom('header').style.transform = 'translateY(0)';
         window.nav_hidden = false;
      }
      window.position_old = window.scrollY;
   });
};
/*}}}*/
// add class on focus{{{
const add_class_on_focus = (list) => {
   for (const key in list) {
      doms(key).forEach((d) => {
         observer_dom(d, (dom) => dom.classList.add(list[key]));
      });
   }
}; //
/*}}}*/
// toggle class on focus{{{
const toggle_class_on_focus = (list) => {
   for (const key in list) {
      doms(key).forEach((d) => {
         observer_dom(
            d,
            (dom) => dom.classList.add(list[key]),
            (dom) => dom.classList.remove(list[key])
         );
      });
   }
}; //
/*}}}*/
// update scroll{{{
const update_scroll = () => {
   window.addEventListener('scroll', () => {
      document.body.style.setProperty(
         '--scroll',
         window.pageYOffset / (document.body.offsetHeight - window.innerHeight)
      );
   });
};
/*}}}*/

